import React, { Component } from 'react';

import tweet from './tweet.css';

class Tweet extends Component {
    constructor(props){
        super(props);

    }
    render() { 
        return ( 
            <div className="tweet">
                <img src={this.props.src}/>
                <p>{this.props.username}</p>
                <p>
                {this.props.tweet}
                </p>
                <span>{this.props.likes} likes</span>
                <span className="retweets">{this.props.retweets} retweets</span>
                <span className="comments">{this.props.comments} comments</span>

            </div>
         );
    }
}
 
export default Tweet;