import React, { Component } from 'react';
import  CsrfToken from '../csrfToken';


class  NewTweet extends Component {
    constructor(props){
        super(props);
        this.state = { 
            csrfMiddleWareToken:'',
         }
        this.textInput = React.createRef();
    }
    handelClick=()=>{
        console.log(this.textInput.textContent);
    }
    getToken=(token)=>{
        this.setState({csrfMiddleWareToken:token});
    }
    render() { 
        return ( 
            <div>
                <form className="new-tweet">
                    <CsrfToken get_token={this.getToken}/>
                    <textarea ref={this.textInput} rows="10" cols="100">
                        Whats on your mind
                    </textarea>
                    <input type="button" onClick={this.handelClick} className="new-tweet-btn" value="submit"/>
                </form>
            </div>
         );
    }
}
 
export default NewTweet;