import React, { Component } from 'react';
import {HashRouter, Route, Switch} from "react-router-dom";
import logo from './logo.svg';
import './App.css';
import './styles.css';
import Nav from './Components/nav/nav.js';
import Tweet from './Components/tweet/tweet.js';
import NewTweet from './Components/tweet/newTweet';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      tweets:[
      
      ]
    }
  }
    componentDidMount=()=>{
      const url = "api/v1/tweets/";
      fetch(url)
      .then(response=>response.json())
      .then(data=>this.setState({tweets:data}))

    }
  
  render(
    ) 
    
    {
      return (
        <div>
          <HashRouter>
              <Nav/>
                <div class="container">
                      <Route exact path="/">
                            <section class="tweets">
                            {this.state.tweets.map((tweet)=>{
                              return <Tweet 
                                        src={""} 
                                        tweet={tweet.tweet} 
                                        username={tweet.username} 
                                        likes={tweet.likes.length} 
                                        comments={tweet.comments.length}
                                        retweets={0}>
                                      </Tweet>
                            })}
                            </section>
                            <section class="profile">
                                  <h1>Profile</h1>
                                  <img src="https://via.placeholder.com/50C/O https://placeholder.com/"/>
                                  <h3>Username: squadran2003</h3>
                                  <h4>10 Followers</h4>
                                  <h4>12 Following</h4>
                            </section>
                      </Route>
                      <Route exact path="/add">
                            <NewTweet/>
                      </Route>
                </div>
                <footer className="footer">
                  <h4>copy right&copy; dr twitter 2020-2022</h4>
                </footer>
          </HashRouter>
        </div>
      );
  }
}
export default App;


